.support {
    z-index: 999;
}
.support-btn {
    background-color: #004085;
    width: 60px;
    font-size: 25px;
    height: 60px;
    color: white;
    border-radius: 50%;
    border: none;
    outline: none;
    position: fixed;
    bottom: 10px;
    right: 10px;
}

.support-body {
    max-width: 250px;
    background-color: white;
    border-radius: 10px;
    padding: 10px;    
    font-size: 15px;
    position: fixed;
    right: 10px;
    bottom: 85px;
}
.support-body .email {
    word-break: break-all;
}
.arrow-down {
    width: 0; 
    height: 0; 
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;    
    border-top: 20px solid white;
    position: fixed;
    right: 20px;
    bottom: 70px;
}