.form-body {
    border-top: 5px solid #28a745;
    max-width: fit-content;
    margin: 10px auto;
}

.digit {
    width: 50px;
    height: 50px;
    margin: 10px;
    text-align: center;
}

.verify {
    width: 200px;
}

.resend {
    text-decoration: underline;
    color: #084298;
    cursor: pointer;
}
.disabled {
    opacity: 0.2;
}
.disabled span{
    cursor: auto;
}