@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');

body {
  padding-top: 20px;
  padding-bottom: 20px;
  background: linear-gradient(90deg, #4b6cb7 0%, #182848 100%);
}

* {
  font-family: 'Comfortaa', cursive;
}

.dashboard-header {
  text-align: center;
  color: white;
  margin-bottom: 20px;
}

.search-box {
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  max-width: 400px;
  margin: 10px auto;
}
.search-box input {
  border: none;
  width: 100%;
  outline-width: 0px !important;
}




.no-records {
  color: white;
  text-align: center;
  margin-top: 20px;
}

.property {
  background-color: white;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  margin: 10px auto;
  max-width: 400px;
}
.property-body {
  margin-left: 10px;
}
.property-body .key {
  font-size: 20px;
  font-weight: bold;
  word-break: break-all;
}
.property-body button {
  color: white !important;
  font-size: 15px;
  margin-right: 10px;
}
.property .value {
  word-break: break-all;
}

.add-secret-btn, .logout-btn, .block-btn, .download-btn {
  color: white !important;
  position: fixed;
  bottom: 10px;
  left: 10px;
  font-size: 30px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: none;
}

.download-btn {
  left: 80px;
  background-color: #004085;
}

.logout-btn {
  left: 150px;
}


.block-btn {
  left: auto;
  right: 80px;
}

.add-property-container {
  background-color: white;
  padding: 10px;
  max-width: 400px;
  margin: 20px auto;
}
.add-property-container .form-group {
  margin: 10px 0px;
}
.add-property-actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.add-property-container button {
  width: 45%;
  margin: 2px 0;
}
.hide {
  display: none;
}


.close-button-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.close-button-container h1 {
  font-size: 25px;
  color: black !important;
}
.close-button{
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 25px;
  margin: 0px;
  width: 50px !important;
}
